import { Component, OnInit }   from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Project }             from './../class/project';

@Component({
  selector: 'dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})

export class DashboardComponent implements OnInit{
	projects: Project[];
  globalProject: Project = new Project();
	selectedProject: Project;
  selectOption: string;

	constructor(private database: AngularFireDatabase){}

	ngOnInit(){
		this.projects = [];
    this.database.list('/global-stats/global').subscribe(data => {
      data.forEach(item => {
        if(item.$key == "patientCount"){
          this.globalProject.patientCount = item.$value;
        }
        else if(item.$key == "idCount"){
          this.globalProject.idCount = item.$value;
        }
      })
      this.globalProject.name = "Global";
    });
	  this.database.list('/global-stats/projects').subscribe(data => {
	    	data.forEach(item => {
          if(item.projectName !== undefined)
	    		  this.cpyInformation(item);
	    	})
        this.projects.sort((a: Project, b: Project) => {
          if(a.name < b.name)
            return -1;
          else if(a.name > b.name)
            return 1;
          else
            return 0;
        })
	    });
  	}

  	cpyInformation(info): void{
  		let newProject: Project = new Project();
  		newProject.create(info.idCount, info.patientCount, info.projectId, 
  						  info.projectName);
  		this.projects.push(newProject);
  	}

  	selectProject(project: Project): void{
  		this.selectedProject = project;
  		this.selectedProject.users = [];
      this.selectOption = "Chart";
  	}

    seeGlobal(): void{
      this.selectProject(this.globalProject);
    }
}



