export class Chart{
	labels: string[];
	type: string;
	legend: boolean;
	data: any[];
	options: any;
	color: any[];
	name: string;
	top: string;
	left: string;
	width: string;
	height: string;

	constructor(){
		this.labels = [];
		this.data = [];
		this.color = [];
	}
}


