import { Component, Input, OnChanges} from '@angular/core';
import { ViewChild }				  from '@angular/core';
import { AngularFireDatabase }        from 'angularfire2/database';
import { Project }                    from './../class/project';
import { User }                       from './../class/user';
import { Session }					  from './../class/session';
import { Coordinate }				  from './../class/coordinate';

@Component({
  selector: 'map',
  templateUrl: './map.component.html'
})

export class MapComponent implements OnChanges {
	@Input() project: Project;
	lat: number = 0;
  	lng: number = 0;
  	e: any;
  	coordinates: Coordinate[] = [];
	constructor(private database: AngularFireDatabase){}

	ngOnChanges(){
		if(this.project !== undefined){
			this.drawMap(0);
		}
	}
	drawMap(index: number): void{
		this.database.list('/project-sessions/' + this.project.id, {
	      query: {
	        orderByChild: 'userId',
	        equalTo: this.project.users[index].id,
	        limitToFirst: 100
	      }
	    }).subscribe(data => {
	    	let val: Coordinate[] = [];
	    	let newData: boolean = false;
	      	data.forEach(item => {
	      		let newCoord = new Coordinate();
	      		if(item.latitude != '' && item.longitude != ''){
	      			newCoord.lat = parseFloat(item.latitude);
	      			newCoord.lng = parseFloat(item.longitude);
	      			if(newData === false){
		      			this.lat = newCoord.lat;
			      		this.lng = newCoord.lng;
			      		newData = true;
			      	}
			      	val.push(newCoord)
	      		}
	      	})
	      	this.coordinates = val;
	    });
	}
}



