import { Component, Input, OnChanges }		  from '@angular/core';
import { ViewChild, ElementRef, Inject }      from '@angular/core';
import { DOCUMENT }                   		  from '@angular/platform-browser';
import { AngularFireDatabase }        		  from 'angularfire2/database';
import { Project }                    		  from './../class/project';
import { User }                       		  from './../class/user';
import { Session }					  		  from './../class/session';
import { PageScrollConfig }           		  from 'ng2-page-scroll';
import { PageScrollService }          		  from 'ng2-page-scroll';
import { PageScrollInstance }         		  from 'ng2-page-scroll';

@Component({
  selector: 'table',
  templateUrl: './table.component.html'
})

export class TableComponent implements OnChanges {
	@Input() project: Project;
	@ViewChild('container') private container: ElementRef;
	showUser: boolean = false;
	settings: any; 
  	data: any = [];
	constructor(private database: AngularFireDatabase, 
				private pageScrollService: PageScrollService,
				@Inject(DOCUMENT) private document: any){
		this.settings = {
		    columns: {
		    	userId: {
		        	title: 'User id',
		        	editable: false
		      },
		    	hardwareVersion: {
		        	title: 'Hardware Version',
		        	editable: false
		      },
		    	startTime: {
		        	title: 'Start Time',
		        	editable: false
		      },
		    	latitude: {
		        	title: 'Latitude',
		        	editable: false
		      },
		    	longitude: {
		        	title: 'Longitude',
		        	editable: false
		      },
		    },
		    actions: false,
		    add: false,
		    hideSubHeader: true,
		    pager:{
		    	perPage: 5
		    }
		};
	}

	ngOnChanges(){
		if(this.project !== undefined){
			this.drawTable(0);
		}
	}

	test(e: any): void{
		console.log(e);
		this.showUser = true;
		let pageScrollInstance: PageScrollInstance = PageScrollInstance.newInstance({document: this.document, scrollTarget: '#test', verticalScrolling: false});
		setTimeout(() => this.pageScrollService.start(pageScrollInstance), 200);
	}
	
	changeUser(index: number): void{
		this.drawTable(index);
		let pageScrollInstance: PageScrollInstance = PageScrollInstance.newInstance({document: this.document, scrollTarget: '#container', verticalScrolling: false});
		this.pageScrollService.start(pageScrollInstance);
		setTimeout(() => this.showUser = false, PageScrollConfig.defaultDuration);
	}

	drawTable(index: number): void{
	    this.database.list('/project-sessions/' + this.project.id, {
	      query: {
	        orderByChild: 'userId',
	        equalTo: this.project.users[index].id,
	        limitToFirst: 1000
	      }
	    }).subscribe(data => {
	      data.forEach(item => {
	        let session: Session = new Session();
	        session.cpyInformation(item);
	        this.project.users[index].sessions.push(session);
	      })
	      this.data = this.project.users[index].sessions;
	    });
  	}
}



