import { Session } from './session';

export class User{
	idCount: number;
	patientCount: number;
	id: string;
	sessions: Array<Session>;
	index: number;

	create(_index: number, _idCount: number, _patientCount: number, _id: string){
		this.index = _index;
		this.idCount = _idCount;
		this.patientCount = _patientCount;
		this.id = _id;
		this.sessions = [];
	}
}


