import { User } from './user'

export class Project{
	idCount: number;
	patientCount: number;
	id: string;
	name: string;
	users: Array<User>;

	create(_idCount: number, _patientCount: number, _id: string, _name: string){
		this.idCount = _idCount;
		this.patientCount = _patientCount;
		this.id = _id;
		this.name = _name;
	}
}


